//5- 5(cinco) minutos de tomada de tempo para qualificação de largada;
// 5 voltas: selecionar o melhor tempo
export class TomadaDeTempo {
    
    constructor(corrida, piloto, tempo) {
        this._corrida = corrida;
        this._piloto = piloto;
        this._tempo = tempo;

        Object.freeze(this);
    }

    get corrida() {
        return this._corrida;
    }
    
    get piloto() {
        return this._piloto;
    }
    
    get tempo() {
        return this._tempo;
    }
}